.header {
  background: white;
  padding: 0;
  border-bottom: 1px solid #23a98c66;
}
.header a {
  color: #290545;
}
.header a:hover {
  background: #82beb166;
  color: white;
  cursor: pointer;
}
.container {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.logo {
  font-weight: bold;
  padding: 1rem;
}

.header ul {
  display: flex;
  list-style-type: none;
  margin: 0;
}

.header ul a {
  padding: 1rem;
  display: inline-block;
}

.menu_container {
  position: relative;
}
.menu {
  position: absolute;
  z-index: 1;
  background-color: whitesmoke;
  display: none;
}

.menu_container:hover .menu {
  display: block;
}

.menu a {
  width: 100%;
  min-width: 8rem;
}
.cart_count {
  background-color: #23a98c66;
  color: white;
  padding: 0.1rem 0.45rem;
  border-radius: 100rem;
  font-size: 0.9rem;
}
